.game-overview {
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  gap: clamp(32px, 6vw, 64px);
  align-items: start;
  margin: clamp(32px, 6vw, 64px) 0;
}

.game-description h2 {
  font-size: 2.2rem;
  font-weight: 900;
  margin-bottom: 24px;
  color: var(--text-primary);
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

.game-description p {
  font-size: 1.125rem;
  line-height: 1.7;
  margin-bottom: 24px;
  color: var(--text-secondary);
  font-weight: 500;
}

.game-description p:last-child {
  margin-bottom: 0;
}

.game-preview-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-xl);
  overflow: hidden;
  transition: transform .3s ease, box-shadow .3s ease, border-color .3s ease, background .3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.game-preview-card:hover {
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.preview-image {
  background: rgba(16, 21, 52, 0.8);
  padding: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 240px;
}

.image-placeholder {
  text-align: center;
  color: var(--text-secondary);
}

.image-placeholder svg {
  margin-bottom: 24px;
  color: #7f85d6;
}

.image-placeholder p {
  font-size: 14px;
  margin: 0;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: .6px;
}

.preview-info {
  padding: 32px;
}

.preview-info h3 {
  font-size: 1.2rem;
  font-weight: 800;
  margin-bottom: 12px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
  text-transform: uppercase;
}

.preview-info p {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 24px;
  line-height: 1.6;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: clamp(16px, 3vw, 24px);
  margin: clamp(24px, 5vw, 48px) 0;
}

.feature-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  padding: 28px;
  transition: transform .25s ease, box-shadow .25s ease, border-color .25s ease, background .25s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.feature-card:hover {
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.feature-icon {
  margin-bottom: 20px;
  color: var(--blurple);
}

.feature-icon svg {
  color: var(--blurple);
}

.feature-card h3 {
  font-size: 1.1rem;
  font-weight: 800;
  margin-bottom: 12px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
  text-transform: uppercase;
}

.feature-card p {
  font-size: 14px;
  line-height: 1.6;
  color: var(--text-secondary);
  font-weight: 500;
  margin: 0;
}

.how-to-play {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: clamp(16px, 3vw, 24px);
  margin: clamp(24px, 5vw, 48px) 0;
}

.instruction {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  padding: 22px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  transition: all .3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.instruction:hover {
  border-color: rgba(88,101,242,0.6);
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
}

.instruction-number {
  background: var(--blurple);
  color: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 1rem;
  flex-shrink: 0;
}

.instruction-content h3 {
  font-size: 1.05rem;
  font-weight: 800;
  margin-bottom: 6px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
  text-transform: uppercase;
}

.instruction-content p {
  font-size: 14px;
  line-height: 1.6;
  color: var(--text-secondary);
  font-weight: 500;
  margin: 0;
}

.development-status {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 24px;
  margin: 48px 0;
}

.status-item {
  text-align: center;
  padding: 24px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  transition: transform .25s ease, box-shadow .25s ease, border-color .25s ease, background .25s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.status-item:hover {
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.status-icon {
  margin-bottom: 12px;
  color: var(--blurple);
}

.status-icon svg {
  color: var(--blurple);
}

.status-item h3 {
  font-size: 0.95rem;
  font-weight: 800;
  margin-bottom: 6px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
  text-transform: uppercase;
}

.status-item p {
  font-size: 13px;
  color: var(--text-secondary);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .5px;
  margin: 0;
}

.status-note {
  text-align: center;
  margin-top: 32px;
}

.status-note p {
  font-size: 1rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
  font-style: italic;
}

/* Beta notice glass morphism */
.beta-notice {
  background: rgba(255, 193, 7, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: var(--radius-lg);
  padding: 20px;
  margin-top: 24px;
  box-shadow: 0 8px 32px rgba(255, 193, 7, 0.1);
}

.beta-notice p {
  margin: 0;
  color: #ffc107;
  font-weight: 600;
}

/* Development approach glass morphism */
.development-approach {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  padding: 32px;
  margin-top: 48px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.development-approach h3 {
  font-size: 1.25rem;
  font-weight: 800;
  margin-bottom: 16px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
}

.development-approach p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
  font-weight: 500;
  margin: 0;
}

@media (max-width: 768px) {
  .game-overview {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .instruction {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 16px;
  }
  
  .development-status {
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }
  
  .game-description h2 {
    font-size: clamp(1.8rem, 6vw, 2.2rem);
  }
  
  .game-description p {
    font-size: clamp(1rem, 4vw, 1.125rem);
  }
  
  .game-preview-card {
    border-radius: var(--radius-lg);
  }
  
  .preview-image {
    padding: 32px 24px;
    min-height: 200px;
  }
  
  .preview-info {
    padding: 24px;
  }
  
  .preview-info h3 {
    font-size: 1.1rem;
  }
  
  .preview-info p {
    font-size: 13px;
    margin-bottom: 20px;
  }
  
  .feature-card {
    padding: 24px;
  }
  
  .feature-card h3 {
    font-size: 1rem;
  }
  
  .feature-card p {
    font-size: 13px;
  }
  
  .how-to-play {
    grid-template-columns: 1fr;
    gap: 16px;
    margin: 24px 0;
  }
  
  .instruction {
    padding: 20px;
  }
  
  .instruction-number {
    width: 36px;
    height: 36px;
    font-size: 0.9rem;
  }
  
  .instruction-content h3 {
    font-size: 1rem;
  }
  
  .instruction-content p {
    font-size: 13px;
  }
  
  .development-status {
    margin: 32px 0;
  }
  
  .status-item {
    padding: 20px;
  }
  
  .status-item h3 {
    font-size: 0.9rem;
  }
  
  .status-item p {
    font-size: 12px;
  }
  
  .status-note {
    margin-top: 24px;
  }
  
  .status-note p {
    font-size: 0.9rem;
  }
  
  .development-approach {
    padding: 24px;
    margin-top: 32px;
  }
  
  .development-approach h3 {
    font-size: 1.1rem;
  }
  
  .development-approach p {
    font-size: 0.9rem;
  }
  
  .beta-notice {
    padding: 16px;
    margin-top: 20px;
  }
  
  .beta-notice p {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .game-overview {
    gap: 32px;
  }
  
  .game-description h2 {
    font-size: clamp(1.5rem, 8vw, 2rem);
    margin-bottom: 20px;
  }
  
  .game-description p {
    font-size: clamp(0.9rem, 4.5vw, 1rem);
    margin-bottom: 20px;
  }
  
  .preview-image {
    padding: 24px 16px;
    min-height: 160px;
  }
  
  .preview-info {
    padding: 20px;
  }
  
  .preview-info h3 {
    font-size: 1rem;
  }
  
  .preview-info p {
    font-size: 12px;
    margin-bottom: 16px;
  }
  
  .features-grid {
    gap: 12px;
    margin: 20px 0;
  }
  
  .feature-card {
    padding: 20px;
  }
  
  .feature-card h3 {
    font-size: 0.9rem;
  }
  
  .feature-card p {
    font-size: 12px;
  }
  
  .how-to-play {
    gap: 12px;
    margin: 20px 0;
  }
  
  .instruction {
    padding: 16px;
    gap: 12px;
  }
  
  .instruction-number {
    width: 32px;
    height: 32px;
    font-size: 0.8rem;
  }
  
  .instruction-content h3 {
    font-size: 0.9rem;
  }
  
  .instruction-content p {
    font-size: 12px;
  }
  
  .development-status {
    grid-template-columns: 1fr;
    gap: 12px;
    margin: 24px 0;
  }
  
  .status-item {
    padding: 16px;
  }
  
  .status-item h3 {
    font-size: 0.8rem;
  }
  
  .status-item p {
    font-size: 11px;
  }
  
  .status-note {
    margin-top: 20px;
  }
  
  .status-note p {
    font-size: 0.8rem;
  }
  
  .development-approach {
    padding: 20px;
    margin-top: 24px;
  }
  
  .development-approach h3 {
    font-size: 1rem;
  }
  
  .development-approach p {
    font-size: 0.8rem;
  }
  
  .beta-notice {
    padding: 14px;
    margin-top: 16px;
  }
  
  .beta-notice p {
    font-size: 12px;
  }
}

/* Touch-friendly interactions for mobile */
@media (hover: none) and (pointer: coarse) {
  .game-preview-card:hover {
    transform: none;
  }
  
  .feature-card:hover {
    transform: none;
  }
  
  .instruction:hover {
    transform: none;
  }
  
  .status-item:hover {
    transform: none;
  }
  
  /* Add active states for touch devices */
  .game-preview-card:active {
    transform: scale(0.98);
  }
  
  .feature-card:active {
    transform: scale(0.98);
  }
  
  .instruction:active {
    transform: scale(0.98);
  }
  
  .status-item:active {
    transform: scale(0.98);
  }
}

/* Landscape mobile optimization */
@media (max-width: 768px) and (orientation: landscape) {
  .game-overview {
    gap: 32px;
  }
  
  .preview-image {
    min-height: 180px;
    padding: 28px 20px;
  }
  
  .features-grid {
    gap: 16px;
    margin: 20px 0;
  }
  
  .how-to-play {
    gap: 16px;
    margin: 20px 0;
  }
  
  .development-status {
    margin: 24px 0;
  }
}
