.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
}

/* Scroll Animation Classes */
.fade-in {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}

.slide-left {
  opacity: 0;
  transform: translateX(-50px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.slide-left.visible {
  opacity: 1;
  transform: translateX(0);
}

.slide-right {
  opacity: 0;
  transform: translateX(50px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.slide-right.visible {
  opacity: 1;
  transform: translateX(0);
}

.slide-up {
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.slide-up.visible {
  opacity: 1;
  transform: translateY(0);
}

.scale-in {
  opacity: 0;
  transform: scale(0.8);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.scale-in.visible {
  opacity: 1;
  transform: scale(1);
}

.rotate-in {
  opacity: 0;
  transform: rotate(-10deg) scale(0.9);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.rotate-in.visible {
  opacity: 1;
  transform: rotate(0deg) scale(1);
}

/* Staggered animations for lists and grids */
.stagger-item {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.stagger-item.visible {
  opacity: 1;
  transform: translateY(0);
}

.stagger-item:nth-child(1) { transition-delay: 0.1s; }
.stagger-item:nth-child(2) { transition-delay: 0.2s; }
.stagger-item:nth-child(3) { transition-delay: 0.3s; }
.stagger-item:nth-child(4) { transition-delay: 0.4s; }
.stagger-item:nth-child(5) { transition-delay: 0.5s; }
.stagger-item:nth-child(6) { transition-delay: 0.6s; }

/* Parallax scroll effects */
.parallax-slow {
  transition: transform 0.1s ease-out;
}

.parallax-medium {
  transition: transform 0.15s ease-out;
}

.parallax-fast {
  transition: transform 0.2s ease-out;
}

/* Enhanced hero section with scroll animations */
.hero-section {
  background: rgba(8, 10, 14, 0.28);
  color: #ffffff;
  padding: clamp(96px, 18vh, 140px) 0 clamp(56px, 10vh, 96px);
  position: relative;
  overflow: hidden;
  z-index: 2;
}

/* Remove overlays for the new hero template */
.hero-2025 { background: transparent; }
.hero-2025::before,
.hero-2025::after { background: none; background-image: none; animation: none; }

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(91, 108, 255, 0.06) 0%, transparent 55%),
    radial-gradient(circle at 80% 20%, rgba(157, 140, 255, 0.06) 0%, transparent 55%);
  pointer-events: none;
  animation: heroGlow 8s ease-in-out infinite;
}

@keyframes heroGlow {
  0%, 100% {
    opacity: 0.5;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}

.hero-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(91, 108, 255, 0.02) 0%, transparent 55%),
    radial-gradient(circle at 75% 75%, rgba(157, 140, 255, 0.02) 0%, transparent 55%);
  animation: float 20s ease-in-out infinite;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  33% {
    transform: translateY(-10px) rotate(1deg);
  }
  66% {
    transform: translateY(10px) rotate(-1deg);
  }
}

.hero-content {
  position: relative;
  z-index: 1;
  text-align: center;
}

.hero-title {
  font-family: 'Ginto', 'Outfit', 'Inter', system-ui, -apple-system, 'Segoe UI', sans-serif;
  font-size: 4.5rem;
  font-weight: 900;
  margin-bottom: 20px;
  letter-spacing: -0.02em;
  line-height: 1.05;
  text-transform: uppercase;
  opacity: 0;
  transform: translateY(30px);
  animation: heroTitleIn 1s cubic-bezier(0.4, 0, 0.2, 1) 0.2s forwards;
}

@keyframes heroTitleIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-subtitle {
  font-size: 1.2rem;
  margin-bottom: 36px;
  color: #374151;
  font-weight: 600;
  max-width: 720px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0;
  transform: translateY(20px);
  animation: heroSubtitleIn 1s cubic-bezier(0.4, 0, 0.2, 1) 0.6s forwards;
}

@keyframes heroSubtitleIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-buttons {
  display: flex;
  gap: 14px;
  justify-content: center;
  flex-wrap: wrap;
  opacity: 0;
  transform: translateY(20px);
  animation: heroButtonsIn 1s cubic-bezier(0.4, 0, 0.2, 1) 1s forwards;
}

.hero-buttons .btn {
  padding: 16px 26px;
  font-size: 15px;
  border-radius: var(--radius-pill);
}

@keyframes heroButtonsIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Floating elements */
.floating-element {
  position: absolute;
  pointer-events: none;
  opacity: 0.1;
  animation: floatElement 6s ease-in-out infinite;
}

.floating-element:nth-child(1) {
  top: 20%;
  left: 10%;
  width: 60px;
  height: 60px;
  background: linear-gradient(45deg, #3b82f6, #8b5cf6);
  border-radius: 50%;
  animation-delay: 0s;
}

.floating-element:nth-child(2) {
  top: 60%;
  right: 15%;
  width: 40px;
  height: 40px;
  background: linear-gradient(45deg, #8b5cf6, #3b82f6);
  border-radius: 50%;
  animation-delay: 2s;
}

.floating-element:nth-child(3) {
  bottom: 30%;
  left: 20%;
  width: 30px;
  height: 30px;
  background: linear-gradient(45deg, #3b82f6, #8b5cf6);
  border-radius: 50%;
  animation-delay: 4s;
}

@keyframes floatElement {
  0%, 100% {
    transform: translateY(0px) rotate(0deg) scale(1);
    opacity: 0.1;
  }
  50% {
    transform: translateY(-20px) rotate(180deg) scale(1.1);
    opacity: 0.3;
  }
}

/* Grid pattern overlay */
.grid-pattern {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    linear-gradient(rgba(59, 130, 246, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(59, 130, 246, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
  animation: gridMove 20s linear infinite;
  pointer-events: none;
}

@keyframes gridMove {
  0% { transform: translate(0, 0); }
  100% { transform: translate(50px, 50px); }
}

/* Enhanced section animations */
.section {
  position: relative;
  z-index: 2;
  overflow: hidden;
  background-image: url('../public/bg-no-hero.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.section .container {
  position: relative;
  z-index: 3;
}



.section-title {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.section-title.visible {
  opacity: 1;
  transform: translateY(0);
}

.section-subtitle {
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0.2s;
}

.section-subtitle.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Enhanced feature animations */
.feature {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: calc(var(--feature-index, 0) * 0.1s);
}

.feature.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Enhanced game preview animations */
.game-preview {
  opacity: 0;
  transform: translateY(40px);
  transition: all 1s cubic-bezier(0.4, 0, 0.2, 1);
}

.game-preview.visible {
  opacity: 1;
  transform: translateY(0);
}

.game-info {
  opacity: 0;
  transform: translateX(-30px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0.3s;
}

.game-info.visible {
  opacity: 1;
  transform: translateX(0);
}

.game-screenshot {
  opacity: 0;
  transform: translateX(30px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0.5s;
}

.game-screenshot.visible {
  opacity: 1;
  transform: translateX(0);
}

/* Enhanced social links animations */
.social-links {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.social-links.visible {
  opacity: 1;
  transform: translateY(0);
}

.social-link {
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.social-link.visible {
  opacity: 1;
  transform: translateY(0);
}

.social-link:nth-child(1) { transition-delay: 0.1s; }
.social-link:nth-child(2) { transition-delay: 0.2s; }
.social-link:nth-child(3) { transition-delay: 0.3s; }

/* Floating stars with scroll parallax */
.floating-stars {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1;
}

.star {
  position: absolute;
  width: 4px;
  height: 4px;
  background: #ffffff;
  border-radius: 50%;
  opacity: 0.6;
  animation: twinkle 3s ease-in-out infinite;
}

.star:nth-child(1) { top: 10%; left: 10%; animation-delay: 0s; }
.star:nth-child(2) { top: 20%; right: 15%; animation-delay: 1s; }
.star:nth-child(3) { top: 40%; left: 20%; animation-delay: 2s; }
.star:nth-child(4) { top: 60%; right: 25%; animation-delay: 0.5s; }
.star:nth-child(5) { top: 80%; left: 30%; animation-delay: 1.5s; }
.star:nth-child(6) { top: 30%; right: 40%; animation-delay: 2.5s; }
.star:nth-child(7) { top: 70%; left: 50%; animation-delay: 0.8s; }
.star:nth-child(8) { top: 90%; right: 60%; animation-delay: 1.8s; }

@keyframes twinkle {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.2); }
}

/* Responsive design */
@media (max-width: 768px) {
  .hero-section { padding: 80px 0 60px; }
  .hero-title { font-size: 2.4rem; }
  .hero-subtitle { font-size: 1.05rem; }
  .hero-buttons { flex-direction: column; align-items: center; }
  .hero-buttons .btn { width: 100%; max-width: 280px; justify-content: center; }
  .floating-element { display: none; }
  
  /* Adjust animation delays for mobile */
  .stagger-item:nth-child(1) { transition-delay: 0.05s; }
  .stagger-item:nth-child(2) { transition-delay: 0.1s; }
  .stagger-item:nth-child(3) { transition-delay: 0.15s; }
  .stagger-item:nth-child(4) { transition-delay: 0.2s; }
  .stagger-item:nth-child(5) { transition-delay: 0.25s; }
  .stagger-item:nth-child(6) { transition-delay: 0.3s; }
}

/* Performance optimizations */
@media (prefers-reduced-motion: reduce) {
  .fade-in, .slide-left, .slide-right, .slide-up, .scale-in, .rotate-in,
  .stagger-item, .feature, .game-preview, .game-info, .game-screenshot,
  .social-links, .social-link, .section-title, .section-subtitle {
    transition: none;
    animation: none;
    opacity: 1;
    transform: none;
  }
  
  .floating-element, .star, .floating-teddy {
    animation: none;
  }
}

/* Scroll to top button */
.scroll-to-top {
  position: fixed;
  bottom: 24px;
  right: 16px;
  width: 56px;
  height: 56px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px) scale(0.8);
  z-index: 1000;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.scroll-to-top.visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) scale(1);
}

.scroll-to-top:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(88, 101, 242, 0.6);
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
}

.scroll-to-top:active {
  transform: translateY(-2px) scale(0.98);
}

.scroll-to-top svg {
  transition: transform 0.2s ease;
}

.scroll-to-top:hover svg {
  transform: translateY(-2px);
}

/* Mobile-first responsive design */
@media (max-width: 1200px) {
  .container {
    padding: 0 24px;
  }
  
  .hero-title {
    font-size: clamp(2.5rem, 8vw, 4.5rem);
  }
  
  .hero-subtitle {
    font-size: clamp(1rem, 4vw, 1.2rem);
  }
}

@media (max-width: 768px) {
  .container {
    padding: 0 20px;
  }
  
  .hero-section {
    padding: clamp(80px, 20vh, 120px) 0 clamp(40px, 8vh, 60px);
    min-height: auto;
  }
  
  .hero-title {
    font-size: clamp(2rem, 10vw, 3rem);
    line-height: 1.1;
  }
  
  .hero-subtitle {
    font-size: clamp(0.9rem, 4.5vw, 1.1rem);
    margin-bottom: 24px;
  }
  
  .hero-buttons {
    flex-direction: column;
    align-items: center;
    gap: 12px;
  }
  
  .hero-buttons .btn {
    width: 100%;
    max-width: 280px;
    justify-content: center;
  }
  
  .floating-element {
    display: none;
  }
  
  .grid-pattern {
    background-size: 30px 30px;
  }
  
  .section {
    padding: clamp(40px, 10vh, 60px) 0;
  }
  
  .section-title {
    font-size: clamp(1.8rem, 6vw, 2.5rem);
    margin-bottom: 16px;
  }
  
  .section-subtitle {
    font-size: clamp(1rem, 4vw, 1.2rem);
    margin-bottom: 32px;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .feature-card {
    padding: 24px;
  }
  
  .game-preview {
    grid-template-columns: 1fr;
    gap: 32px;
    text-align: center;
  }
  
  .game-info {
    order: 2;
  }
  
  .game-screenshot {
    order: 1;
  }
  
  .social-links {
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }
  
  .social-link {
    width: 100%;
    max-width: 280px;
    justify-content: center;
  }
  
  .floating-stars {
    display: none;
  }
  
  .scroll-to-top {
    bottom: 20px;
    right: 20px;
    width: 48px;
    height: 48px;
  }
  
  .scroll-to-top svg {
    width: 18px;
    height: 18px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 16px;
  }
  
  .hero-section {
    padding: clamp(70px, 18vh, 100px) 0 40px;
  }
  
  .hero-title {
    font-size: clamp(1.8rem, 12vw, 2.5rem);
  }
  
  .hero-subtitle {
    font-size: clamp(0.85rem, 5vw, 1rem);
  }
  
  .section {
    padding: 32px 0;
  }
  
  .section-title {
    font-size: clamp(1.5rem, 8vw, 2rem);
  }
  
  .feature-card {
    padding: 20px;
  }
  
  .game-preview {
    gap: 24px;
  }
  
  .hero-features {
    flex-direction: column;
    gap: 20px;
  }
  
  .hero-feature {
    text-align: center;
  }
  
  .floating-stars {
    display: none;
  }
}

/* Touch-friendly interactions for mobile */
@media (hover: none) and (pointer: coarse) {
  .btn:hover {
    transform: none;
  }
  
  .feature-card:hover {
    transform: none;
  }
  
  .social-link:hover {
    transform: none;
  }
  
  .scroll-to-top:hover {
    transform: none;
  }
  
  /* Add active states for touch devices */
  .btn:active {
    transform: scale(0.98);
  }
  
  .feature-card:active {
    transform: scale(0.98);
  }
  
  .social-link:active {
    transform: scale(0.98);
  }
  
  .scroll-to-top:active {
    transform: scale(0.95);
  }
}

/* Landscape mobile optimization */
@media (max-width: 768px) and (orientation: landscape) {
  .hero-section {
    padding: 40px 0 30px;
    min-height: auto;
  }
  
  .hero-title {
    font-size: clamp(1.8rem, 6vw, 2.5rem);
  }
  
  .section {
    padding: 30px 0;
  }
}
