/* Blog specific styles */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  z-index: 2;
}

.blog-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  text-align: center;
}

.blog-loading p {
  color: #ffffff;
  font-size: 1.2rem;
  margin-top: 1rem;
}

/* Override hero description color for blog */
.hero-section .hero-description {
  color: #666 !important;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top: 4px solid #ffffff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.blog-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.blog-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.4);
}

.card-content {
  padding: 1.5rem;
}

.card-title {
  font-size: 1.5rem;
  color: #ffffff;
  margin-bottom: 1rem;
  line-height: 1.3;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.card-excerpt {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.card-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
}

.no-posts {
  text-align: center;
  padding: 4rem 2rem;
  color: #ffffff;
  font-size: 1.1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Full post view */
.blog-post-full {
  min-height: 100vh;
  background: url('../../public/bg-blog.jpg') no-repeat center center fixed;
  background-size: cover;
  padding: 2rem 0;
  position: relative;
}

.blog-post-full::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1;
}

.blog-post-full .container {
  max-width: none;
  margin: 0;
  padding: 0 2rem;
  position: relative;
  z-index: 2;
  width: 100%;
}

.back-button {
  background: #3b82f6;
  color: #ffffff;
  font-size: 1rem;
  cursor: pointer;
  padding: 0.875rem 1.75rem;
  margin-bottom: 0;
  display: flex;
  align-items: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 8px;
  font-weight: 600;
  border: none;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.back-button-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.back-button:hover {
  background: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.4);
}

/* Translation Controls */
.translation-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
  padding: 0;
  background: transparent;
  backdrop-filter: none;
  border-radius: 0;
  border: none;
  position: relative;
  z-index: 99999;
}

.language-selector {
  position: relative;
  z-index: 99999;
}

.language-button {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(15px);
  position: relative;
  overflow: hidden;
  min-width: 160px;
  justify-content: center;
  text-align: center;
}

.language-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
}

.language-button:hover {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.2));
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.3);
}

.language-button:hover::before {
  left: 100%;
}

.dropdown-arrow {
  font-size: 0.8rem;
  transition: transform 0.3s ease;
}

.language-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  padding: 0.75rem;
  margin-top: 0.5rem;
  z-index: 99999;
  max-height: 350px;
  overflow-y: auto;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.language-dropdown::-webkit-scrollbar {
  display: none;
}

.language-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  width: 100%;
  padding: 0.875rem 1rem;
  background: transparent;
  border: none;
  color: #ffffff;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.3s ease;
  text-align: left;
  font-weight: 500;
  position: relative;
  overflow: hidden;
  justify-content: flex-start;
  line-height: 1;
}

.language-option::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.5s ease;
}

.language-option:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateX(5px);
}

.language-option:hover::before {
  left: 100%;
}

.language-option.active {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.3), rgba(59, 130, 246, 0.1));
  color: #60a5fa;
  border: 1px solid rgba(59, 130, 246, 0.5);
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
}

.language-flag {
  font-size: 1.2rem;
  min-width: 1.5rem;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}

.language-name {
  font-size: 0.9rem;
  flex: 1;
  display: flex;
  align-items: center;
  line-height: 1;
}



.translated-content {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.translation-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.translation-badge {
  background: rgba(59, 130, 246, 0.2);
  color: #60a5fa;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
}

.show-original-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.85rem;
  transition: all 0.2s ease;
}

.show-original-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.5);
}

.translated-text {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.8;
  font-size: 1.1rem;
}

.blog-post-content {
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 4rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  margin: 2rem 0;
  position: relative;
  z-index: 2;
  grid-column: 2;
  order: 2;
}

.post-header {
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 2px solid rgba(255, 255, 255, 0.3);
}

.post-title {
  font-size: 3rem;
  color: #ffffff;
  margin-bottom: 1.5rem;
  line-height: 1.2;
  font-weight: 700;
  animation: slideInLeft 0.6s ease-out;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.post-meta {
  display: flex;
  gap: 1.5rem;
  font-size: 1rem;
  color: #ffffff;
  font-weight: 500;
  animation: slideInRight 0.6s ease-out 0.2s both;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.post-meta time,
.post-meta .post-date,
.post-meta b,
.post-meta strong {
  color: #ffffff !important;
}

.post-author {
  color: #007bff;
  font-weight: 500;
}

.post-body {
  line-height: 1.8;
  color: #ffffff;
  font-size: 1.125rem;
  animation: fadeIn 0.8s ease-out 0.4s both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.post-body a {
  color: #3b82f6;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: all 0.2s ease;
}

.post-body a:hover {
  color: #2563eb;
  border-bottom-color: #2563eb;
}

.post-body ul, .post-body ol {
  margin: 1.5rem 0;
  padding-left: 2rem;
}

.post-body li {
  margin-bottom: 0.5rem;
  line-height: 1.7;
}

.post-body p {
  margin-bottom: 1.5rem;
}

.post-body img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin: 2rem 0;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.post-body h1,
.post-body h2,
.post-body h3,
.post-body h4,
.post-body h5,
.post-body h6 {
  color: #ffffff;
  margin-top: 2.5rem;
  margin-bottom: 1.25rem;
  font-weight: 600;
  line-height: 1.3;
}

.post-body h1 { font-size: 2rem; }
.post-body h2 { font-size: 1.75rem; }
.post-body h3 { font-size: 1.5rem; }
.post-body h4 { font-size: 1.25rem; }
.post-body h5 { font-size: 1.1rem; }
.post-body h6 { font-size: 1rem; }

.post-body p {
  margin-bottom: 1.5rem;
}

.post-body ul,
.post-body ol {
  margin-bottom: 1.5rem;
  padding-left: 2rem;
}

.post-body li {
  margin-bottom: 0.5rem;
}

.post-body blockquote {
  border-left: 4px solid #3b82f6;
  padding-left: 1.5rem;
  margin: 2rem 0;
  font-style: italic;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.5);
  padding: 1.5rem;
  border-radius: 8px;
  font-size: 1.1rem;
  line-height: 1.7;
}

.post-body code {
  background: rgba(255, 255, 255, 0.1);
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', 'Source Code Pro', monospace;
  font-size: 0.9em;
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.2);
  font-weight: 500;
}

.post-body pre {
  background: #1e293b;
  padding: 1.5rem;
  border-radius: 8px;
  overflow-x: auto;
  margin: 2rem 0;
  border: 1px solid #334155;
  color: #e2e8f0;
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', 'Source Code Pro', monospace;
  font-size: 0.9rem;
  line-height: 1.6;
}

.post-body pre code {
  background: none;
  padding: 0;
}

/* Blog Post Layout */
.blog-post-layout {
  display: grid !important;
  grid-template-columns: 300px 1fr !important;
  gap: 2rem;
  align-items: start;
  width: 100%;
  margin: 0;
  padding: 0;
}

/* Table of Contents Sidebar */
.toc-sidebar {
  position: sticky;
  top: 2rem;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 1.5rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  max-height: calc(100vh - 4rem);
  overflow-y: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  grid-column: 1;
  order: 1;
}

.toc-sidebar::-webkit-scrollbar {
  display: none;
}

.toc-header {
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.toc-header h3 {
  color: #ffffff;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.toc-loading {
  font-size: 0.85rem;
  color: #fbbf24;
  font-weight: 500;
}

.toc-nav {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.toc-item {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  padding: 0.75rem;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.3s ease;
  text-align: left;
  font-size: 0.9rem;
  line-height: 1.4;
}

.toc-item:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  transform: translateX(5px);
}

.toc-number {
  color: #60a5fa;
  font-weight: 600;
  min-width: 1.5rem;
  font-size: 0.85rem;
}

.toc-title {
  flex: 1;
}

.toc-empty {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
  padding: 2rem 0;
}

/* Mobile-First Responsive Design */
@media (max-width: 1200px) {
  .container {
    padding: 1.5rem;
  }
  
  .blog-post-layout {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .toc-sidebar {
    position: static;
    width: 100%;
    max-height: none;
    overflow: visible;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
  }
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  /* Blog Grid Mobile */
  .blog-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .blog-card {
    margin: 0 0.5rem;
  }
  
  .card-content {
    padding: 1.25rem;
  }
  
  .card-title {
    font-size: 1.25rem;
  }
  
  .card-excerpt {
    font-size: 0.9rem;
  }
  
  /* Blog Post Full Mobile */
  .blog-post-full .container {
    padding: 0 1rem;
  }
  
  .blog-post-layout {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .blog-post-content {
    order: 1;
    grid-column: 1;
    padding: 1.5rem;
    margin: 1rem 0;
  }
  
  .post-title {
    font-size: 2rem;
    line-height: 1.2;
  }
  
  .post-meta {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
  
  .post-body {
    font-size: 0.95rem;
    line-height: 1.6;
  }
  
  .post-body h1, .post-body h2, .post-body h3 {
    font-size: 1.5rem;
    margin: 1.5rem 0 1rem 0;
  }
  
  .post-body h4, .post-body h5, .post-body h6 {
    font-size: 1.25rem;
    margin: 1.25rem 0 0.75rem 0;
  }
  
  .post-body p {
    margin-bottom: 1rem;
  }
  
  .post-body blockquote {
    margin: 1rem 0;
    padding: 1rem;
    font-size: 0.9rem;
  }
  
  .post-body code {
    font-size: 0.85rem;
    padding: 0.25rem 0.5rem;
  }
  
  .post-body pre {
    margin: 1rem 0;
    padding: 1rem;
  }
  
  /* TOC Sidebar Mobile */
  .toc-sidebar {
    order: 2;
    grid-column: 1;
    margin-top: 1rem;
  }
  
  .toc-header {
    padding: 1rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  .toc-header h3 {
    font-size: 1.25rem;
  }
  
  .toc-nav {
    padding: 1rem;
  }
  
  .toc-item {
    padding: 0.75rem;
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
  }
  
  .toc-number {
    font-size: 0.8rem;
  }
  
  .toc-title {
    font-size: 0.9rem;
  }
  
  /* Back Button & Language Selector Mobile */
  .back-button-container {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
    padding: 1rem;
  }
  
  .back-button {
    align-self: flex-start;
    padding: 0.75rem 1.25rem;
    font-size: 0.9rem;
  }
  
  .language-selector {
    align-self: flex-end;
  }
  
  .language-button {
    min-width: 140px;
    padding: 0.75rem 1rem;
    font-size: 0.9rem;
  }
  
  .language-dropdown {
    max-height: 250px;
    max-width: 200px;
    right: 0;
  }
  
  .language-option {
    padding: 0.75rem 1rem;
    font-size: 0.9rem;
  }
  
  /* Translation Controls Mobile */
  .translation-controls {
    flex-direction: column;
    align-items: stretch;
    gap: 0.75rem;
    margin: 1rem 0;
  }
  
  .language-dropdown {
    max-height: 200px;
  }
  
  /* Comments Section Mobile */
  .comments-section {
    padding: 1.5rem;
    margin-top: 2rem;
  }
  
  .comments-title {
    font-size: 1.5rem;
  }
  
  .comments-icon {
    width: 24px;
    height: 24px;
  }
  
  .comment-form {
    padding: 1.5rem;
  }
  
  .comment-input {
    padding: 1rem;
    font-size: 0.95rem;
  }
  
  .comment-submit-btn {
    width: 100%;
    padding: 0.875rem 1.5rem;
  }
  
  .comment-item {
    padding: 1.25rem;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .comment-avatar {
    width: 40px;
    height: 40px;
  }
  
  .avatar-text {
    font-size: 1rem;
  }
  
  .comment-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .comment-author-info {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .comment-actions {
    flex-wrap: wrap;
    gap: 0.75rem;
  }
  
  .comment-action-btn {
    padding: 0.5rem 0.75rem;
    font-size: 0.8rem;
  }
  
  /* Reply Form Mobile */
  .reply-form {
    padding: 1.25rem;
    margin-top: 0.75rem;
  }
  
  .reply-input {
    padding: 0.875rem;
    font-size: 0.9rem;
  }
  
  .reply-form-footer {
    flex-direction: column;
    gap: 0.75rem;
    align-items: stretch;
  }
  
  .reply-submit-btn {
    width: 100%;
  }
  
  /* Comment Replies Mobile */
  .comment-replies {
    margin-left: 1rem;
    padding-left: 0.75rem;
  }
  
  .reply-item {
    padding: 0.875rem;
    margin-bottom: 0.5rem;
  }
  
  .reply-avatar {
    width: 32px;
    height: 32px;
  }
  
  .avatar-text {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0.75rem;
  }
  
  /* Extra Small Mobile */
  .blog-post-full .container {
    padding: 0 0.75rem;
  }
  
  .blog-post-content {
    padding: 1rem;
    margin: 0.5rem 0;
  }
  
  .post-title {
    font-size: 1.75rem;
    margin-bottom: 1rem;
  }
  
  .post-meta {
    font-size: 0.85rem;
  }
  
  .post-body {
    font-size: 0.9rem;
  }
  
  .post-body h1, .post-body h2, .post-body h3 {
    font-size: 1.4rem;
    margin: 1.25rem 0 0.75rem 0;
  }
  
  .post-body h4, .post-body h5, .post-body h6 {
