/* Enhanced Comments Section */
.comments-section {
  margin-top: 3rem;
  padding: 4rem;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 2;
}

.comments-header {
  text-align: center;
  margin-bottom: 2rem;
}

.comments-title {
  color: #ffffff;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  justify-content: center;
}

.comments-icon {
  width: 28px;
  height: 28px;
  color: #60a5fa;
}

.comments-subtitle {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
  font-style: italic;
}

.comment-form {
  margin-bottom: 2.5rem;
  background: rgba(255, 255, 255, 0.05);
  padding: 2rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.comment-form-header {
  margin-bottom: 1rem;
}

.author-input-group {
  display: flex;
  gap: 1rem;
}

.author-input {
  flex: 1;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 8px;
  color: #ffffff;
  font-size: 0.9rem;
  backdrop-filter: blur(10px);
}

.author-input:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.6);
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
}

.author-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.comment-input {
  width: 100%;
  padding: 1.25rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  color: #ffffff;
  font-size: 1rem;
  resize: vertical;
  margin-bottom: 1rem;
  backdrop-filter: blur(10px);
  line-height: 1.6;
}

.comment-input:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.6);
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
}

.comment-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.comment-form-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.comment-char-count {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
  font-style: italic;
}

.comment-submit-btn {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.9), rgba(37, 99, 235, 0.9));
  color: white;
  border: 1px solid rgba(59, 130, 246, 0.4);
  padding: 0.875rem 1.75rem;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
}

.comment-icon {
  width: 18px;
  height: 18px;
}

.loading-spinner {
  width: 16px;
  height: 16px;
