}

.comment-submit-btn:disabled {
  background: rgba(59, 130, 246, 0.4);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.comments-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.comments-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
