.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 64px;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.about-text h2 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 24px;
  background: linear-gradient(135deg, var(--text-primary) 0%, var(--accent) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.2;
}

/* Mobile title override */
@media (max-width: 768px) {
  .about-text h2::before {
    content: "About Us";
    display: block;
    background: linear-gradient(135deg, var(--text-primary) 0%, var(--accent) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .about-text h2 {
    font-size: 0; /* Hide the original text */
  }
}

.about-text p {
  font-size: 1.125rem;
  line-height: 1.7;
  margin-bottom: 24px;
  color: var(--text-secondary);
  font-weight: 500;
}

.about-text p:last-child {
  margin-bottom: 0;
}

.about-visual {
  display: flex;
  justify-content: center;
  align-items: center;
}

.visual-placeholder {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-xl);
  padding: 80px 60px;
  text-align: center;
  color: var(--text-secondary);
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all .3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.visual-placeholder:hover {
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.visual-placeholder svg {
  margin-bottom: 32px;
  color: #7f85d6;
}

.visual-placeholder p {
  font-size: 14px;
  margin: 0;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .6px;
}

.process-timeline {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin: 48px 0;
}

.process-step {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  padding: 24px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  transition: all .3s ease;
}

.process-step:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-2px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
}

.step-number {
  background: var(--blurple);
  color: #fff;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 1rem;
  flex-shrink: 0;
}

.step-content h3 {
  font-size: 1.1rem;
  font-weight: 800;
  margin-bottom: 8px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
  text-transform: uppercase;
}

.step-content p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
  font-weight: 500;
  margin: 0;
}

.community-links {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-top: 48px;
}

.community-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  padding: 20px 24px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  text-decoration: none;
  color: var(--text-primary);
  transition: transform .25s ease, box-shadow .25s ease, border-color .25s ease, background .25s ease;
  min-width: 200px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.community-link:hover {
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.community-link svg {
  color: var(--blurple);
}

.community-link span {
  font-weight: 800;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: .6px;
}

/* Glass morphism for grid cards */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin: 48px 0;
}

.card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  padding: 32px;
  text-align: center;
  transition: all .3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.card:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
}

.card-icon {
  background: rgba(88,101,242,0.1);
  width: 64px;
  height: 64px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  border: 1px solid rgba(88,101,242,0.3);
}

.card-icon svg {
  color: var(--blurple);
}

.card h3 {
  font-size: 1.25rem;
  font-weight: 800;
  margin-bottom: 16px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
}

.card p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
  font-weight: 500;
  margin: 0;
}

@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .process-step {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 16px;
  }
  
  .community-links {
    flex-direction: column;
    align-items: center;
  }
  
  .community-link {
    min-width: 280px;
  }
  
  .about-text h2 {
    font-size: clamp(1.8rem, 6vw, 2.2rem);
  }
  
  .about-text p {
    font-size: clamp(1rem, 4vw, 1.125rem);
  }
  
  .visual-placeholder {
    padding: 60px 40px;
    min-height: 300px;
  }
  
  .visual-placeholder svg {
    width: 60px;
    height: 60px;
    margin-bottom: 24px;
  }
  
  .process-timeline {
    gap: 20px;
    margin: 32px 0;
  }
  
  .process-step {
    padding: 20px;
  }
  
  .step-number {
    width: 36px;
    height: 36px;
    font-size: 0.9rem;
  }
  
  .step-content h3 {
    font-size: 1rem;
  }
  
  .step-content p {
    font-size: 0.9rem;
  }
  
  .community-links {
    margin-top: 32px;
    gap: 16px;
  }
  
  .community-link {
    padding: 16px 20px;
    min-width: 240px;
  }
  
  .community-link svg {
    width: 20px;
    height: 20px;
  }
  
  .community-link span {
    font-size: 12px;
  }
  
  .grid {
    grid-template-columns: 1fr;
    gap: 20px;
    margin: 32px 0;
  }
  
  .card {
    padding: 24px;
  }
  
  .card-icon {
    width: 56px;
    height: 56px;
    margin-bottom: 16px;
  }
  
  .card h3 {
    font-size: 1.1rem;
  }
  
  .card p {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .about-content {
    gap: 32px;
  }
  
  .about-text h2 {
    font-size: clamp(1.5rem, 8vw, 2rem);
    margin-bottom: 20px;
  }
  
  .about-text p {
    font-size: clamp(0.9rem, 4.5vw, 1rem);
    margin-bottom: 20px;
  }
  
  .visual-placeholder {
    padding: 40px 24px;
    min-height: 240px;
  }
  
  .visual-placeholder svg {
    width: 48px;
    height: 48px;
    margin-bottom: 20px;
  }
  
  .visual-placeholder p {
    font-size: 12px;
  }
  
  .process-timeline {
    gap: 16px;
    margin: 24px 0;
  }
  
  .process-step {
    padding: 16px;
  }
  
  .step-number {
    width: 32px;
    height: 32px;
    font-size: 0.8rem;
  }
  
  .step-content h3 {
    font-size: 0.9rem;
  }
  
  .step-content p {
    font-size: 0.8rem;
  }
  
  .community-links {
    margin-top: 24px;
    gap: 12px;
  }
  
  .community-link {
    padding: 14px 16px;
    min-width: 200px;
  }
  
  .community-link svg {
    width: 18px;
    height: 18px;
  }
  
  .community-link span {
    font-size: 11px;
  }
  
  .grid {
    gap: 16px;
    margin: 24px 0;
  }
  
  .card {
    padding: 20px;
  }
  
  .card-icon {
    width: 48px;
    height: 48px;
    margin-bottom: 12px;
  }
  
  .card h3 {
    font-size: 1rem;
  }
  
  .card p {
    font-size: 0.8rem;
  }
}

/* Touch-friendly interactions for mobile */
@media (hover: none) and (pointer: coarse) {
  .visual-placeholder:hover {
    transform: none;
  }
  
  .process-step:hover {
    transform: none;
  }
  
  .community-link:hover {
    transform: none;
  }
  
  .card:hover {
    transform: none;
  }
  
  /* Add active states for touch devices */
  .visual-placeholder:active {
    transform: scale(0.98);
  }
  
  .process-step:active {
    transform: scale(0.98);
  }
  
  .community-link:active {
    transform: scale(0.98);
  }
  
  .card:active {
    transform: scale(0.98);
  }
}

/* Landscape mobile optimization */
@media (max-width: 768px) and (orientation: landscape) {
  .about-content {
    gap: 32px;
  }
  
  .visual-placeholder {
    min-height: 200px;
    padding: 40px 30px;
  }
  
  .process-timeline {
    gap: 16px;
    margin: 24px 0;
  }
  
  .process-step {
    padding: 16px;
  }
}
