.footer {
  background-image: url('../../public/bg-footer.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  color: var(--text-primary);
  border-top: 1px solid var(--border);
  padding: 64px 0 24px;
  margin-top: auto;
  position: relative;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 48px;
  margin-bottom: 48px;
}

.footer-section h3 {
  font-size: 0.95rem;
  font-weight: 900;
  margin-bottom: 18px;
  color: var(--text-primary);
  text-transform: uppercase;
  letter-spacing: 0.6px;
}

.footer-logo { 
  font-size: 1.6rem; 
  font-weight: 900; 
  margin-bottom: 16px; 
  display: flex; 
  align-items: center; 
  gap: 6px; 
  font-family: 'Ginto', 'Outfit', 'Inter', sans-serif; 
}

.logo-text { color: var(--blurple); }
.logo-team { 
  color: var(--text-primary); 
  position: relative;
}

/* Remove black pill background for footer logo team */
.footer .logo-team::before {
  display: none;
}

.footer-description { line-height: 1.6; color: var(--text-secondary); margin-bottom: 20px; font-size: 14px; }

.footer-links { list-style: none; padding: 0; }
.footer-links li { margin-bottom: 12px; }
.footer-links a { color: var(--text-secondary); text-decoration: none; transition: color .2s ease; font-size: 14px; }
.footer-links a:hover { color: var(--text-primary); }

.footer-section .social-links { display: flex; flex-direction: column; gap: 12px; }
.footer-section .social-link { display: inline-flex; align-items: center; gap: 10px; color: var(--text-primary); text-decoration: none; transition: transform .2s ease, color .2s ease; font-size: 14px; }
.footer-section .social-link:hover { transform: translateY(-2px); color: #fff; }
.footer-section .social-icon { font-size: 16px; }

.footer-bottom { border-top: 1px solid var(--border); padding-top: 20px; }

.footer-bottom-content { display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap; gap: 24px; }
.footer-bottom p { color: var(--text-secondary); margin: 0; font-size: 14px; }

.footer-bottom-links { display: flex; gap: 20px; }
.footer-bottom-links a { color: var(--text-secondary); text-decoration: none; transition: color .2s ease; font-size: 14px; }
.footer-bottom-links a:hover { color: var(--text-primary); }

.footer-credit {
  margin-top: 16px;
  text-align: center;
  border-top: 1px solid var(--border);
  padding-top: 16px;
}

.footer-credit p {
  color: var(--text-secondary);
  margin: 0;
  font-size: 13px;
  font-weight: 500;
}

.footer-credit a {
  color: var(--blurple);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s ease, text-decoration 0.2s ease;
}

.footer-credit a:hover {
  color: var(--text-primary);
  text-decoration: underline;
}

@media (max-width: 768px) { .footer { padding: 56px 0 24px; } .footer-content { grid-template-columns: 1fr; gap: 32px; } .footer-bottom-content { flex-direction: column; text-align: center; } .footer-bottom-links { justify-content: center; } }
