@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400;500;600;700;800;900&family=Inter:wght@300;400;500;600;700&family=JetBrains+Mono:wght@400;500&display=swap');


:root {
  /* Modern black dashboard palette */
  --bg-deep: #0b0d10;
  --bg-top: #0e1116;
  --bg-bottom: #0a0c10;
  --blurple: #5b6cff;
  --blurple-600: #4958d8;
  --blurple-700: #3b47b8;
  --fuchsia: #9d8cff;
  --teal: #22e0ff;
  --text-primary: #e6e9ef;
  --text-secondary: #a5adba;
  --surface: #11151c;
  --surface-2: #0f1420;
  --border: #1e2430;
  --shadow-strong: 0 20px 60px rgba(0,0,0,0.6);
  --radius-xl: 20px;
  --radius-lg: 16px;
  --radius-pill: 9999px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:
    radial-gradient(900px 500px at 0% -10%, rgba(91,108,255,0.08), transparent 50%),
    radial-gradient(900px 500px at 110% 10%, rgba(157,140,255,0.08), transparent 45%),
    linear-gradient(180deg, var(--bg-top), var(--bg-bottom));
  color: var(--text-primary);
  line-height: 1.6;
  overflow-x: hidden;
  position: relative;
}



h1, h2, h3, .section-title, .hero-title {
  font-family: 'Ginto', 'Outfit', 'Inter', system-ui, -apple-system, 'Segoe UI', sans-serif;
}

code {
  font-family: 'JetBrains Mono', 'Courier New', monospace;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: clamp(16px, 4vw, 32px);
  padding-right: clamp(16px, 4vw, 32px);
}

.btn {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 14px 22px;
  background: var(--text-primary);
  color: #0a0a0a;
  text-decoration: none;
  border-radius: var(--radius-pill);
  font-weight: 600;
  font-size: 14px;
  letter-spacing: .2px;
  transition: transform .25s ease, box-shadow .25s ease, background .25s ease, color .25s ease;
  border: none;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.25), transparent);
  transition: left .5s;
}

.btn:hover::before { left: 100%; }

.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(255,255,255,0.12);
}

.btn-secondary {
  background: transparent;
  color: #374151;
  border: 1px solid var(--border);
}

.btn-secondary:hover {
  background: var(--blurple);
  color: #ffffff;
  border-color: var(--blurple);
}

.btn-accent {
  background: var(--blurple);
  color: #ffffff;
}

.btn-accent:hover {
  background: var(--blurple-600);
  box-shadow: 0 14px 36px rgba(88,101,242,0.35);
}

.section {
  padding: clamp(56px, 10vh, 96px) 0;
  position: relative;
}

.section-title {
  font-size: 2.75rem;
  font-weight: 900;
  margin-bottom: 16px;
  color: var(--text-primary);
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

.section-subtitle {
  font-size: 1.125rem;
  margin-bottom: 48px;
  color: var(--text-secondary);
  font-weight: 500;
}

.card {
  background: linear-gradient(180deg, rgba(17,21,28,0.9), rgba(15,20,32,0.92));
  border: 1px solid var(--border);
  border-radius: var(--radius-xl);
  padding: 32px;
  transition: transform .3s ease, box-shadow .3s ease, border-color .3s ease;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.card:hover {
  border-color: rgba(88,101,242,0.6);
  transform: translateY(-6px);
  box-shadow: var(--shadow-strong);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: clamp(16px, 3vw, 24px);
  margin: clamp(24px, 5vw, 40px) 0;
}

.text-gradient {
  background: linear-gradient(135deg, #6B7280, #9CA3AF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Effects */
.particles { position: fixed; top: 0; left: 0; width: 100%; height: 100%; pointer-events: none; z-index: 1; }
.particle { position: absolute; width: 2px; height: 2px; background: var(--blurple); border-radius: 50%; animation: float 8s ease-in-out infinite; opacity: .35; }

@keyframes float {
  0%, 100% { transform: translateY(0) rotate(0); opacity: .35; }
  50% { transform: translateY(-22px) rotate(180deg); opacity: .7; }
}

.fade-in { opacity: 0; transform: translateY(30px); transition: all .6s ease; }
.fade-in.visible { opacity: 1; transform: translateY(0); }
.slide-left { opacity: 0; transform: translateX(-50px); transition: all .6s ease; }
.slide-left.visible { opacity: 1; transform: translateX(0); }
.slide-right { opacity: 0; transform: translateX(50px); transition: all .6s ease; }
.slide-right.visible { opacity: 1; transform: translateX(0); }
.scale-in { opacity: 0; transform: scale(.9); transition: all .6s ease; }
.scale-in.visible { opacity: 1; transform: scale(1); }

@media (max-width: 768px) {
  .section { padding: 48px 0; }
  .section-title { font-size: 2.2rem; }
  .grid { grid-template-columns: 1fr; gap: 16px; }
}
