.navbar { 
  background: transparent; 
  backdrop-filter: none; 
  border-bottom: none; 
  position: absolute; 
  top: 0; 
  width: 100%; 
  z-index: 1000; 
  transition: all 0.2s ease; 
  box-shadow: none;
}

.navbar-container { max-width: 1280px; margin: 0 auto; padding: 0 32px; display: flex; justify-content: space-between; align-items: center; height: 72px; }

.navbar-logo { 
  text-decoration: none; 
  color: var(--text-primary); 
  font-size: 1.25rem; 
  font-weight: 900; 
  display: flex; 
  align-items: center; 
  gap: 6px; 
  font-family: 'Ginto', 'Outfit', 'Inter', sans-serif; 
  letter-spacing: -0.02em;
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  transition: all 0.2s ease;
  cursor: pointer;
}

.navbar-logo:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(59, 130, 246, 0.3);
  transform: translateY(-1px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.logo-text { color: var(--accent); }
.logo-team { 
  color: #ffffff !important; 
  position: relative;
  z-index: 2;
}

/* Black pill background for TEAM text */
.logo-team::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -4px;
  right: -4px;
  bottom: -2px;
  background: #000000;
  border-radius: 20px;
  z-index: -1;
  transition: opacity 0.3s ease;
}

/* Hide the black pill when scrolled */
.navbar.scrolled .logo-team::before {
  opacity: 0;
}

.navbar-menu { display: flex; align-items: center; gap: 28px; }

.navbar-link { 
  color: #000000; 
  text-decoration: none; 
  font-weight: 600; 
  font-size: 14px; 
  transition: all 0.2s ease; 
  position: relative; 
  padding: 10px 18px; 
  border-radius: 12px; 
  letter-spacing: 0.025em;
  background: transparent;
  border: none;
  backdrop-filter: none;
  cursor: pointer;
}

.navbar-link:hover, .navbar-link.active { 
  color: #8B5CF6; 
  background: transparent;
  border: none;
  box-shadow: none;
  transform: none;
}



.navbar-toggle { display: none; flex-direction: column; cursor: pointer; gap: 4px; }

.bar { width: 24px; height: 2px; background: var(--text-primary); transition: all 0.2s ease; border-radius: 1px; }

/* Remove account area for gaming site */
.navbar-right { display: none; }

@media (max-width: 768px) {
  .navbar-container { height: 64px; padding: 0 20px; }
  
  .navbar-menu { 
    position: absolute; 
    top: 100%; 
    left: 0; 
    width: 100%; 
    background: rgba(255, 255, 255, 0.95); 
    backdrop-filter: blur(20px); 
    -webkit-backdrop-filter: blur(20px);
    flex-direction: column; 
    justify-content: flex-start; 
    padding: 16px 0; 
    gap: 0; 
    transition: all 0.3s ease; 
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-top: none;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    border-radius: 0 0 16px 16px;
  }
  
  .navbar-menu.active { 
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
  
  .navbar-link { 
    font-size: 16px; 
    padding: 16px 24px; 
    margin: 0; 
    border-radius: 0;
    color: #000000;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    transition: all 0.2s ease;
    text-align: left;
    justify-content: flex-start;
    width: 100%;
  }
  
  .navbar-link:last-child {
    border-bottom: none;
  }
  
  .navbar-link:hover, .navbar-link.active { 
    color: #8B5CF6; 
    background: rgba(139, 92, 246, 0.05);
    transform: none;
  }
  
  .navbar-toggle { display: flex; }
  
  /* Make hamburger icon black in mobile */
  .bar { 
    background: #000000; 
    width: 24px; 
    height: 2px; 
  }
}
