/* Hero Section - Completely Redesigned */
.hero-section {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 100px 0;
  overflow: hidden;
}

/* Hero Background Effects */
.hero-background {
  position: absolute;
  inset: 0;
  pointer-events: none;
  z-index: 0;
  background-image: url('../../public/bg.webp');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.hero-gradient {
  position: absolute;
  inset: 0;
  background: none;
}

.hero-grid-pattern {
  position: absolute;
  inset: 0;
  background: none;
}

@keyframes gridFloat {
  0%, 100% { transform: translate(0, 0); opacity: 0.3; }
  50% { transform: translate(-30px, -30px); opacity: 0.1; }
}

.hero-orbs {
  position: absolute;
  inset: 0;
  display: none;
}

.hero-orb {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(59, 130, 246, 0.1), transparent 70%);
  filter: blur(40px);
  animation: orbFloat 8s ease-in-out infinite;
}

.hero-orb-1 {
  width: 400px;
  height: 400px;
  top: 15%;
  left: 5%;
  animation-delay: 0s;
}

.hero-orb-2 {
  width: 300px;
  height: 300px;
  top: 20%;
  right: 10%;
  animation-delay: 2s;
}

.hero-orb-3 {
  width: 250px;
  height: 250px;
  bottom: 25%;
  left: 50%;
  animation-delay: 4s;
}

@keyframes orbFloat {
  0%, 100% { transform: translate(0, 0) scale(1); opacity: 0.4; }
  33% { transform: translate(20px, -30px) scale(1.1); opacity: 0.6; }
  66% { transform: translate(-15px, 20px) scale(0.9); opacity: 0.3; }
}

/* Hero Content Layout */
.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 900px;
  margin: 0 auto 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-section .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 50px;
  color: #ffffff;
  font-size: 13px;
  font-weight: 600;
  margin-bottom: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.hero-badge svg {
  color: var(--accent);
}

.hero-title {
  font-family: 'Ginto', 'Outfit', 'Inter', sans-serif;
  font-size: clamp(2.5rem, 7vw, 4.5rem);
  font-weight: 900;
  line-height: 1.1;
  margin-bottom: 24px;
  color: #000000;
  letter-spacing: -0.02em;
  text-align: center;
  max-width: 800px;
}

.hero-description {
  font-size: clamp(1.125rem, 2vw, 1.25rem);
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}



.hero-actions {
  display: flex;
  gap: 12px;
  justify-content: center;
  flex-wrap: wrap;
}

.hero-cta {
  padding: 12px 24px;
  font-size: 15px;
  font-weight: 700;
}

/* Floating stars effect for sections */
.floating-stars {
  position: absolute;
  inset: 0;
  pointer-events: none;
  z-index: 1;
  overflow: hidden;
  will-change: transform;
}

.star {
  position: absolute;
  width: 2px;
  height: 2px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  animation: starFloat 8s ease-in-out infinite;
  will-change: transform, opacity;
  backface-visibility: hidden;
}

.star:nth-child(1) { top: 20%; left: 10%; animation-delay: 0s; }
.star:nth-child(2) { top: 40%; left: 80%; animation-delay: 2s; }
.star:nth-child(3) { top: 60%; left: 20%; animation-delay: 4s; }
.star:nth-child(4) { top: 80%; left: 70%; animation-delay: 6s; }
.star:nth-child(5) { top: 30%; left: 50%; animation-delay: 1s; }
.star:nth-child(6) { top: 70%; left: 30%; animation-delay: 3s; }
.star:nth-child(7) { top: 50%; left: 90%; animation-delay: 5s; }
.star:nth-child(8) { top: 90%; left: 40%; animation-delay: 7s; }

@keyframes starFloat {
  0%, 100% { 
    transform: translateY(0) scale(1); 
    opacity: 0.6; 
  }
  50% { 
    transform: translateY(-20px) scale(1.2); 
    opacity: 1; 
  }
}

/* Ensure content stability */
.section {
  position: relative;
  min-height: 200px;
}

.game-preview {
  position: relative;
  z-index: 2;
  min-height: 300px;
}

.game-info, .game-screenshot {
  position: relative;
  z-index: 2;
}

/* Force content visibility */
.section-title, .section-subtitle, .game-info h3, .game-info p, .game-features, .btn {
  position: relative;
  z-index: 3;
  opacity: 1 !important;
  visibility: visible !important;
}

/* Prevent content from disappearing */
.game-info, .game-screenshot {
  opacity: 1 !important;
  visibility: visible !important;
  transform: none !important;
}

/* Lively animations for sections */
.section-title {
  animation: titleSlideIn 0.8s ease-out;
}

.section-subtitle {
  animation: subtitleFadeIn 1s ease-out 0.2s both;
}

.game-info {
  animation: slideInLeft 0.8s ease-out 0.3s both;
}

.game-screenshot {
  animation: slideInRight 0.8s ease-out 0.4s both;
}

.feature {
  animation: featurePopIn 0.6s ease-out both;
  animation-delay: calc(var(--feature-index, 0) * 0.1s + 0.5s);
}

.btn {
  animation: buttonBounce 0.6s ease-out 0.8s both;
}

/* Animation keyframes */
@keyframes titleSlideIn {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes subtitleFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes featurePopIn {
  from {
    opacity: 0;
    transform: scale(0.8) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes buttonBounce {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Hover effects for interactivity */
.feature:hover {
  transform: translateY(-5px) scale(1.02);
  transition: all 0.3s ease;
}

.btn:hover {
  transform: translateY(-2px) scale(1.02);
  transition: all 0.3s ease;
}

.game-preview:hover {
  transform: translateY(-3px);
  transition: all 0.3s ease;
}

/* Storm Dash Logo */
.game-logo-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 280px;
}

.storm-dash-logo {
  width: 120px;
  height: 120px;
  object-fit: contain;
  animation: logoFloat 4s ease-in-out infinite;
  filter: drop-shadow(0 8px 16px rgba(0, 0, 0, 0.3));
}

.logo-glow {
  position: absolute;
  width: 140px;
  height: 140px;
  background: radial-gradient(circle, rgba(59, 130, 246, 0.2) 0%, transparent 70%);
  border-radius: 50%;
  animation: glowPulse 3s ease-in-out infinite;
}

@keyframes logoFloat {
  0%, 100% { transform: translateY(0) rotate(0deg); }
  50% { transform: translateY(-10px) rotate(2deg); }
}

@keyframes glowPulse {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.6; transform: scale(1.1); }
}



/* Hero Showcase */
.hero-showcase {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 40px;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.hero-game-preview {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.game-preview-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px 24px 0 0;
}

.game-preview-controls {
  display: flex;
  gap: 8px;
}

.control-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.control-red { background: #EF4444; }
.control-yellow { background: #F59E0B; }
.control-green { background: #10B981; }

.game-preview-title {
  font-size: 14px;
  font-weight: 600;
  color: #000000;
}

.game-preview-content {
  aspect-ratio: 16 / 9;
  position: relative;
}

.game-preview-video {
  width: 100%;
  height: 100%;
  border: none;
}

.hero-features {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.hero-feature {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  transition: all 0.2s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.hero-feature:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(59, 130, 246, 0.3);
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 16px 48px rgba(0, 0, 0, 0.4), 0 0 0 1px rgba(255, 255, 255, 0.1), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.hero-feature-icon {
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--accent);
  backdrop-filter: blur(10px);
}

.hero-feature-content h3 {
  font-size: 16px;
  font-weight: 700;
  color: #000000;
  margin-bottom: 4px;
  line-height: 1.3;
}

/* Make specific feature texts white for better readability */
.hero-feature:nth-child(1) .hero-feature-content h3,
.hero-feature:nth-child(2) .hero-feature-content h3,
.hero-feature:nth-child(3) .hero-feature-content h3 {
  color: #ffffff;
  transition: color 0.6s ease;
}

/* Auto-transition to black text when scrolling past hero section */
.hero-feature:nth-child(1) .hero-feature-content h3.scrolled,
.hero-feature:nth-child(2) .hero-feature-content h3.scrolled,
.hero-feature:nth-child(3) .hero-feature-content h3.scrolled {
  color: #000000;
}

/* Hero feature icons transition */
.hero-feature-icon svg {
  transition: stroke 0.6s ease;
}

.hero-feature-content p {
  font-size: 14px;
  color: #374151;
  margin: 0;
  line-height: 1.4;
}

/* Make specific feature descriptions white for better readability */
.hero-feature:nth-child(1) .hero-feature-content p,
.hero-feature:nth-child(2) .hero-feature-content p,
.hero-feature:nth-child(3) .hero-feature-content p {
  color: #ffffff;
  transition: color 0.6s ease;
}

/* Auto-transition to black text when scrolling past hero section */
.hero-feature:nth-child(1) .hero-feature-content p.scrolled,
.hero-feature:nth-child(2) .hero-feature-content p.scrolled,
.hero-feature:nth-child(3) .hero-feature-content p.scrolled {
  color: #000000;
}

/* Remove fintech eyebrow helper; hero already styled */
.ticker-eyebrow { display: none; }

/* Responsive Design */
@media (max-width: 1024px) {
  .hero-showcase {
    grid-template-columns: 1fr;
    gap: 32px;
    max-width: 800px;
  }
  
  .hero-features {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .hero-feature {
    flex: 1;
    min-width: 280px;
    max-width: 350px;
  }
}

@media (max-width: 768px) {
  .hero-section {
    padding: 80px 0 60px;
    min-height: auto;
  }
  
  .hero-content {
    margin-bottom: 40px;
    max-width: 100%;
  }
  
  .hero-title {
    font-size: clamp(2rem, 10vw, 3rem);
    line-height: 1.1;
  }
  
  .hero-subtitle {
    font-size: clamp(0.9rem, 4.5vw, 1.1rem);
  }
  
  .hero-actions {
    flex-direction: column;
    align-items: center;
    gap: 12px;
  }
  
  .hero-actions .btn {
    width: 100%;
    max-width: 280px;
    justify-content: center;
  }
  
  .hero-features {
    flex-direction: column;
    gap: 20px;
  }
  
  .hero-feature {
    min-width: auto;
    text-align: center;
  }
  
  .hero-orb-1 {
    width: 200px;
    height: 200px;
  }
  
  .hero-orb-2 {
    width: 150px;
    height: 150px;
  }
  
  .hero-orb-3 {
    width: 100px;
    height: 100px;
  }
  
  .game-preview {
    grid-template-columns: 1fr;
    gap: 32px;
    text-align: center;
  }
  
  .game-info {
    order: 2;
  }
  
  .game-screenshot {
    order: 1;
  }
  
  .game-features {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .feature {
    text-align: center;
  }
  
  .social-links {
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }
  
  .social-link {
    width: 100%;
    max-width: 280px;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .hero-section {
    padding: 60px 0 40px;
  }
  
  .hero-title {
    font-size: clamp(1.8rem, 12vw, 2.5rem);
  }
  
  .hero-subtitle {
    font-size: clamp(0.85rem, 5vw, 1rem);
  }
  
  .hero-actions .btn {
    padding: 14px 20px;
    font-size: 14px;
  }
  
  .hero-feature {
    padding: 20px;
  }
  
  .hero-feature-icon {
    margin-bottom: 16px;
  }
  
  .hero-feature-content h3 {
    font-size: 1.1rem;
  }
  
  .hero-feature-content p {
    font-size: 13px;
  }
  
  .game-preview {
    gap: 24px;
  }
  
  .game-info h3 {
    font-size: 1.5rem;
  }
  
  .game-info p {
    font-size: 14px;
  }
  
  .game-features {
    gap: 12px;
  }
  
  .feature {
    padding: 16px;
  }
  
  .feature svg {
    width: 18px;
    height: 18px;
  }
  
  .feature span {
    font-size: 13px;
  }
  
  .social-link {
    padding: 16px 20px;
  }
  
  .social-link svg {
    width: 20px;
    height: 20px;
  }
  
  .social-link span {
    font-size: 12px;
  }
}

/* Touch-friendly interactions for mobile */
@media (hover: none) and (pointer: coarse) {
  .hero-actions .btn:hover {
    transform: none;
  }
  
  .hero-feature:hover {
    transform: none;
  }
  
  .game-preview:hover {
    transform: none;
  }
  
  .feature:hover {
    transform: none;
  }
  
  .social-link:hover {
    transform: none;
  }
  
  /* Add active states for touch devices */
  .hero-actions .btn:active {
    transform: scale(0.98);
  }
  
  .hero-feature:active {
    transform: scale(0.98);
  }
  
  .game-preview:active {
    transform: scale(0.98);
  }
  
  .feature:active {
    transform: scale(0.98);
  }
  
  .social-link:active {
    transform: scale(0.98);
  }
}

/* Landscape mobile optimization */
@media (max-width: 768px) and (orientation: landscape) {
  .hero-section {
    padding: 50px 0 40px;
    min-height: auto;
  }
  
  .hero-title {
    font-size: clamp(1.8rem, 6vw, 2.5rem);
  }
  
  .hero-content {
    margin-bottom: 30px;
  }
  
  .hero-features {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 16px;
  }
  
  .hero-feature {
    flex: 1;
    min-width: 200px;
  }
}

.hero-card { width: 820px; max-width: calc(100% - 40px); perspective: 1200px; }
.hero-card-inner { position: relative; border-radius: 24px; transform-style: preserve-3d; transition: transform .2s ease; }
.hero-card-frame { background: linear-gradient(180deg, var(--surface), var(--surface-2)); border: 1px solid var(--border); border-radius: 24px; padding: 28px; box-shadow: var(--shadow-strong); transform: translateZ(40px); }
.hero-card-screen { display: flex; flex-direction: column; align-items: center; justify-content: center; gap: 16px; background: #101534; border-radius: 16px; min-height: 280px; color: var(--text-secondary); border: 1px solid var(--border); }
.hero-card-screen svg { color: var(--blurple); }
.hero-card-screen p { font-weight: 800; letter-spacing: .6px; text-transform: uppercase; margin: 0; }
.hero-card-glow { position: absolute; inset: -10%; background: radial-gradient(500px 300px at 20% 0%, rgba(88,101,242,.2), transparent 60%), radial-gradient(400px 200px at 80% 80%, rgba(155,132,255,.2), transparent 60%); filter: blur(30px); transform: translateZ(0); border-radius: 32px; pointer-events: none; }

.hero-tilt { will-change: transform; }

@media (max-width: 768px) {
  .hero-card { width: 100%; }
  .hero-card-frame { padding: 20px; }
  .hero-card-screen { min-height: 220px; }
}

.game-preview {
  display: grid;
  grid-template-columns: 1.1fr 0.9fr;
  gap: clamp(32px, 6vw, 64px);
  align-items: center;
  margin: clamp(32px, 6vw, 64px) 0;
}

/* Remove ticker styles for gaming site */
.exchange-preview, .ticker-row, .ticker-track, .ticker-item { display: none; }

/* Remove exchange preview layout */
.preview-grid, .preview-sidebar, .sidebar-title, .sidebar-link, .preview-main { display: none; }

/* Remove wallet/user card styles */
.wallet-card, .wallet-header, .wallet-balance-label, .wallet-balance-value, .wallet-actions,
.user-card, .user-meta, .user-name, .user-email, .user-badges, .badge, .badge-premium, .badge-warning, .badge-danger { display: none; }

.game-info h3 {
  font-size: 2.2rem;
  font-weight: 900;
  margin-bottom: 24px;
  color: #000000;
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

.game-info h3::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 40px;
  height: 2px;
  background: linear-gradient(90deg, var(--blurple), var(--fuchsia));
  border-radius: 1px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.4s ease;
}

.game-info:hover h3::after {
  transform: scaleX(1);
}

.game-info p {
  font-size: 1.125rem;
  line-height: 1.7;
  margin-bottom: 32px;
  color: var(--text-secondary);
  font-weight: 500;
}

.game-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: clamp(12px, 3vw, 20px);
  margin-bottom: clamp(20px, 4vw, 32px);
}

.feature {
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 600;
  padding: 16px 20px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  box-shadow: var(--shadow-subtle);
}

.feature:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
}

.feature svg {
  color: var(--accent);
  flex-shrink: 0;
  transition: all 0.2s ease;
}

.feature:hover svg {
  transform: scale(1.1);
}

.game-screenshot {
  display: flex;
  justify-content: center;
  align-items: center;
}

.screenshot-placeholder {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  padding: 48px 32px;
  text-align: center;
  color: var(--text-secondary);
  min-height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease;
  position: relative;
  box-shadow: var(--shadow-subtle);
}

.screenshot-placeholder:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
}

.screenshot-placeholder svg {
  margin-bottom: 20px;
  color: var(--text-muted);
  transition: all 0.2s ease;
}

.screenshot-placeholder:hover svg {
  color: var(--accent);
  transform: scale(1.05);
}

.screenshot-placeholder p {
  font-size: 14px;
  margin: 0;
  font-weight: 600;
  letter-spacing: 0.05em;
  transition: color 0.2s ease;
}

.screenshot-placeholder:hover p {
  color: var(--accent);
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-top: 48px;
}

.social-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  padding: 24px 28px;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  text-decoration: none;
  color: var(--text-primary);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  min-width: 180px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.social-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.4s ease;
}

.social-link::after {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 50% 0%, rgba(59, 130, 246, 0.1), transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.social-link:hover::before {
  transform: scaleX(1);
}

.social-link:hover::after {
  opacity: 1;
}

.social-link:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(59, 130, 246, 0.4);
  transform: translateY(-6px) scale(1.02);
  box-shadow: 0 16px 48px rgba(0, 0, 0, 0.4), 0 0 0 1px rgba(255, 255, 255, 0.1), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.social-link svg {
  width: 24px;
  height: 24px;
  color: var(--text-secondary);
  transition: all 0.3s ease;
  z-index: 1;
  position: relative;
}

.social-link:hover svg {
  color: #3b82f6;
  transform: scale(1.1) translateY(-2px);
  filter: drop-shadow(0 4px 12px rgba(59, 130, 246, 0.3));
}

.social-link span {
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.025em;
  transition: all 0.3s ease;
  z-index: 1;
  position: relative;
}

.social-link:hover span {
  color: var(--text-primary);
  transform: translateY(-1px);
}

.card-icon {
  margin-bottom: 24px;
  color: var(--blurple);
  transition: all 0.3s ease;
}

.card-icon svg {
  color: #3b82f6;
  transition: all 0.3s ease;
}

.card:hover .card-icon svg {
  transform: scale(1.1) rotate(5deg);
  filter: drop-shadow(0 0 12px rgba(59, 130, 246, 0.4));
}

.card h3 {
  font-size: 1.2rem;
  font-weight: 800;
  margin-bottom: 16px;
  color: var(--text-primary);
  letter-spacing: -0.01em;
  text-transform: uppercase;
  transition: color 0.3s ease;
}

.card:hover h3 {
  color: #3b82f6;
}

.card p {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
  font-weight: 500;
  transition: color 0.3s ease;
}

.card:hover p {
  color: #d4d4d8;
}

/* Advanced hover effects */
.card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.4);
}

/* Staggered animation delays */
.fade-in:nth-child(1) { animation-delay: 0.1s; }
.fade-in:nth-child(2) { animation-delay: 0.2s; }
.fade-in:nth-child(3) { animation-delay: 0.3s; }

.slide-left:nth-child(1) { animation-delay: 0.2s; }
.slide-right:nth-child(1) { animation-delay: 0.4s; }

.scale-in:nth-child(1) { animation-delay: 0.1s; }
.scale-in:nth-child(2) { animation-delay: 0.2s; }

@media (max-width: 768px) {
  .game-preview {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  /* No exchange preview on mobile either */
  
  .game-features {
    grid-template-columns: 1fr;
  }
  
  .social-links {
    flex-direction: column;
    align-items: center;
  }
  
  .social-link {
    min-width: 240px;
  }
}

.hero-game-card {
  position: relative;
  width: 400px;
  height: 250px;
  perspective: 1000px;
  transform-style: preserve-3d;
}

.game-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.3s ease;
}

.game-card-glow {
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  background: linear-gradient(45deg, var(--discord-blurple), var(--discord-green), var(--discord-blurple));
  border-radius: var(--discord-radius-lg);
  opacity: 0.3;
  filter: blur(20px);
  z-index: -1;
  animation: glowPulse 3s ease-in-out infinite;
}

.game-video-container {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 25px;
  overflow: hidden;
  box-shadow: none;
  border: 0;
}

.game-preview-video {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 25px;
}

.hero-game-card:hover .game-card-inner {
  transform: rotateY(5deg) rotateX(5deg);
}

.hero-game-card:hover .game-card-glow {
  opacity: 0.6;
  filter: blur(15px);
}

/* Floating support button */
.support-fab { position: fixed; right: 24px; bottom: 24px; z-index: 1001; background: linear-gradient(135deg, var(--blurple), var(--fuchsia)); color: #fff; border: 0; border-radius: 999px; padding: 14px 18px; font-weight: 900; letter-spacing: .4px; box-shadow: 0 10px 30px rgba(0,123,255,0.35); cursor: pointer; transition: transform .2s ease, box-shadow .2s ease, filter .2s ease; }
.support-fab:hover { transform: translateY(-2px); box-shadow: 0 16px 40px rgba(0,123,255,0.5); filter: brightness(1.08); }
